.searchDetail{
    width: 100%;
    margin: 0 auto;
    .movieInfo{
        position: relative;
        .search-backImg{
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 0;
            min-height: 100vh;
            position: relative;
            img{
                width: 100%;
                height: 100vh;
            }
            &::before{
                background: linear-gradient(0deg,#16161a 0,hsla(0deg,0%,9%,.987) 1.62%,hsla(0deg,0%,9%,.951) 3.1%,hsla(0deg,0%,9%,.896) 4.5%,hsla(0deg,0%,9%,.825) 5.8%,hsla(0deg,0%,9%,.741) 7.06%,hsla(0deg,0%,9%,.648) 8.24%,hsla(0deg,0%,9%,.55) 9.42%,hsla(0deg,0%,9%,.45) 10.58%,hsla(0deg,0%,9%,.352) 11.76%,hsla(0deg,0%,9%,.259) 12.94%,hsla(0deg,0%,9%,.175) 14.2%,hsla(0deg,0%,9%,.104) 15.5%,hsla(0deg,0%,9%,.049) 16.9%,hsla(0deg,0%,9%,.013) 18.38%,hsla(0deg,0%,9%,0) 20%);
                content: "";
                height: 100%;
                left: 0;
                position: absolute;
                width: 100%;
                bottom: 0;
            }
            &::after{
                background: linear-gradient(to right, rgba(0, 0, 0, 0.93), rgba(12, 12, 12, 0.33), transparent);
                content: "";
                height: 100%;
                left: 0;
                position: absolute;
                width: 100%;
                bottom: 0;
            }
        }
        .search-info{
            max-width: 52%;
            padding: 0 2rem;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            gap: 1rem;
            position: absolute;
            top: 33%;
            z-index: 11;
            .search-title{
                font-size: 50px;
                font-weight: bold;
                color: #fffffe;
                margin-bottom: 0;
            }
            .search-originTit{
                font-size: 40px;
                color: #fffffe;
                font-style: italic;
                margin: 0;
            }
            .search-desc{
                font-size: 20px;
                color: #fffffe;
            }
            .search-release{
                color: #fffffe;
            }
        }
    }
    .creditInfo{
        margin-top: 0px;
        width: 100%;
        padding: 0 2rem;
        box-sizing: border-box;
        .search-actors{
            h3{
                color: #fffffe;
                font-size: 30px;
            }
            .mySwiper{
                position: relative;
                // overflow: hidden;
                .swiper-wrapper{
                    display: flex;
                    position: absolute;
                    .swiper-slide{
                        .actor-img{
                            img{
                                width: 100%;
                            }
                        }
                        .actor-info{
                            position: absolute;
                            bottom: 0;
                            /* left: 0; */
                            width: 100%;
                            /* height: 33%; */
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            /* align-items: center; */
                            padding: 30px;
                            box-sizing: border-box;
                            color: rgba(255, 255, 255, 0.8);
                            &:hover{
                                opacity: 1;
                                transition: .5s;
                                background: #16161a71;
                            }
                            .actor-name{
                                font-size: 20px;
                                color: #fffffe;
                                cursor: pointer;
                            }
                            .actor-char{
                                font-size: 20px;
                                color: #fffffe;
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }
}